@import "src/common/styles/sass/variables.scss";
@import "src/common/styles/sass/mixins";

.contactsBlock {
  margin-bottom: $marginBottomBlock;

  .container {
    @include container();
    @include flex(column, flex-start, center, wrap);

    .contactsForm {
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      @media screen and (max-width: 900px) {
        width: 100%;
      }
      .inputArea,
      .textArea {
        width: 100%;
        border: 1px solid $textColor;
        box-sizing: border-box;
        outline: none;
        color: $textColor;
        margin-bottom: 1.2rem;
        &:-webkit-autofill {
          border: 1px solid $textColor;
          -webkit-text-fill-color: $textColor;
          -webkit-box-shadow: 0 0 0 1000px $textColor inset;
          transition: background-color 5000s ease-in-out 0s;
          &:focus{
            border: 2px solid #4a4a4a;
          }
        }

      }
      .inputArea {
        height: calc(1.5em + .75em + 2px);
        padding: .4rem 15px;
        &:focus {
          border: 2px solid;
          transition: all .4s ease-in-out 0s;
        }
      }
      .textArea {
        min-height: 160px;
        padding: 15px;
        resize: none;
        &:focus{
          border: 2px solid;
          transition: all .4s ease-in-out 0s;
        }
      }
      .errorMessage {
        color: #f8241c;
        font-size: 20px;
        font-style: normal;
        font-weight: normal;
        line-height: 26px;
      }
      .btn {
        @include btn();
        &:hover {
          transform: scale(1.1);
        }
      }

    }
  }
}



